<template>
<el-container style="height: 100vh;">
  <div class="mask" @click="maskCloseMenu"></div>
  <el-aside class="aside-nav" :class="{'aside-nav-isCollapse':data.isCollapse}">
    <el-menu
      :uniqueOpened="true"
      :default-active="data.currentRoute.path"
      class="el-menu-vertical-demo"
      @select="handleSelect"
      @open="handleOpen"
      @close="handleClose"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      :router="true"
      :collapse="false"
    >
      <el-submenu index="1">
        <template #title>
          <i class="el-icon-s-home"></i>
          <span>首页</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="/">统计</el-menu-item>
        </el-menu-item-group>
        <el-submenu index="1-2">
          <template #title>关于</template>
          <el-menu-item index="/contact">联系我们</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="/businessmanage">
        <i class="el-icon-menu"></i>
        <template #title>业务管理</template>
      </el-menu-item>
      <el-menu-item index="3" disabled>
        <i class="el-icon-document"></i>
        <template #title>权限管理</template>
      </el-menu-item>
      <el-menu-item index="/settings">
        <i class="el-icon-setting"></i>
        <template #title>设置</template>
      </el-menu-item>
    </el-menu>
  </el-aside>
  <el-container>
    <el-header class="header" style="margin: 0;">
      <nav class="top-nav flex-rCenter animate__animated animate__bounce">
        <el-button style="margin-left: 1rem;" type="" icon="el-icon-menu" @click="menuSwitch"></el-button>
        <h3 @click="test" style="color: white; word-break: keep-all; text-overflow: ellipsis;">后台管理系统</h3>
        <el-button @click="logout" style="margin-right: 0.5rem" type="primary" plain>注销</el-button>
      </nav>
      <div class="breadcrumb flex-rCenter">
        <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 1rem">
          <template v-for="item in data.navList" :key="item.display">
            <template v-if="item.matched">
              <template v-if="item.name==data.currentRoute.name">
                <el-breadcrumb-item :to="match.path?{path:match.path}:null" v-for="match in item.matched"  :key="match.display">{{ match.display }}</el-breadcrumb-item>
              </template>
            </template>
            <template v-else>
              <el-breadcrumb-item v-if="item.name==data.currentRoute.name" :to="null">{{ item.display }}</el-breadcrumb-item>
            </template>
          </template>
        </el-breadcrumb>
      </div>
    </el-header>
    <el-main class="flex-rCenter">
      <router-view data-aos="fade-down"></router-view>
    </el-main>
  </el-container>
</el-container>
</template>

<script lang="ts">
import {
  reactive,
  defineComponent,
  watch,
  ref,
  markRaw,
  onBeforeUpdate,
  inject,
  onBeforeMount,
  computed,
  onUpdated
} from 'vue';
import Statistics from '@/components/Statistics.vue';
import {Router, useRouter } from "vue-router";
import {Store, useStore} from "vuex";
import NProgress from "nprogress";
import nProgress from "nprogress";

export default defineComponent({
  name: 'Layout',
  setup(props, context) {
    const router:Router = useRouter();
    const store:Store<any> = useStore();
    const velocity:any = inject('velocity');
    const currenMediaWidth = computed(()=> store.state.currenMediaWidth);
    const navListEnum = {
      index: {display: '首页', path: '/'},
      statistics: {name: 'Statistics', display: '统计', path: '/'},
      about: {display: '关于'},
      contact: {name: 'Contact', display: '联系我们', path: '/contact'},
      businessManage: {name: 'BusinessManage', display: '业务管理', path: '/businessmanage'},
      settings: {name: 'Settings', display: '设置', path: '/settings'}
    }
    let data = reactive({
      isCollapse: false,
      currentRoute: router.currentRoute.value,
      navList: {
        index: {
          display: navListEnum.index.display
        },
        statistics: {
          name: navListEnum.statistics.name,
          display: navListEnum.statistics.display,
          path: navListEnum.statistics.path,
          matched: [
            navListEnum.index,
            navListEnum.statistics
          ]
        },
        about: {
          display: navListEnum.about.display
        },
        contact: {
          name: navListEnum.contact.name,
          display: navListEnum.contact.display,
          path: navListEnum.contact.path,
          matched: [
            navListEnum.index,
            navListEnum.about,
            navListEnum.contact
          ]
        },
        businessManage: {
          name: navListEnum.businessManage.name,
          display: navListEnum.businessManage.display,
          path: navListEnum.businessManage.path
        },
        settings: {
          name: navListEnum.settings.name,
          display: navListEnum.settings.display,
          path: navListEnum.settings.path
        }
      }
    });
    onBeforeMount(()=>{
      if (window.matchMedia(store.state.media_md).matches) {
        data.isCollapse = true;
      }else{
        data.isCollapse = false;
      }
    });
    onBeforeUpdate(()=>{
      data.currentRoute = router.currentRoute.value;
    });
    onUpdated(()=>{
      test();
      NProgress.start();
      NProgress.done();
    });
    function menuSwitch(){
      if (window.matchMedia(store.state.media_md).matches) {
        let menu:any = document.querySelector('.aside-nav');
        let mask:any = document.querySelector('.mask');
        menu.classList.add('aside-nav-open');
        velocity(mask, {
          opacity: 1
        }, {
          duration: 400,
          display: 'block'
        });
      } else {
        data.isCollapse=!data.isCollapse;
      }
    }
    function maskCloseMenu(e:any){
      let menu:any = document.querySelector('.aside-nav');
      let mask:any = e.target;
      menu.classList.remove('aside-nav-open');
      velocity(menu, {
        xxx: null
      }, {
        duration: 400,
        begin: (els:any)=>{
          els[0].style.setProperty('position', 'absolute');
          els[0].style.setProperty('height', '100%');
          els[0].style.setProperty('z-index', '10');
        },
        complete: (els:any)=>{
          els[0].style.setProperty('position', null);
          els[0].style.setProperty('height', null);
          els[0].style.setProperty('z-index', null);
        }
      });
      velocity(mask, {
        opacity: 0
      }, {
        duration: 400,
        display: 'none'
      });
    }
    watch(currenMediaWidth, (nv, ov)=>{
      if (window.matchMedia(store.state.media_md).matches) {
        data.isCollapse = true;
      }else{
        data.isCollapse = false;
      }
    });
    function logout(){
      window.localStorage.removeItem('userToken');
      router.push({path: '/login'});
    }
    function test(){

      console.log('test')

    }
    const handleSelect = (key:any, keyPath:any) => {return null;};
    const handleOpen = (key:any, keyPath:any) => {return null;};
    const handleClose = (key:any, keyPath:any) => {return null;};
    return {
      data,
      menuSwitch,
      maskCloseMenu,
      logout,
      test,
      handleSelect,
      handleOpen,
      handleClose
    };
  },
});


</script>

